import React from 'react'

export default function Social({children, link="#", tooltip=link}) {

  return (
    <a href={link} title={tooltip} target='_blank' className='py-7 px-9 m-1 rounded-3xl bg-neutral-900 dark:bg-neutral-200 dark:text-neutral-900 flex items-center text-lg text-neutral-50 hover:bg-gray-900 hover:rounded-2xl max-md:px-8 max-md:text-md max-md:w-100 fade-up'>
    {/* // <a href={link} className='py-7 px-9 m-1 rounded-3xl bg-white border-2 border-neutral-800 flex items-center text-lg text-neutral-900 hover: hover:bg-gray-800 hover:duration-500 max-md:px-8 max-md:text-md max-md:w-100 fade-up'> */}
        {children}
    </a>
  )
}
